*{margin:0;padding:0;font-family:"Pretendard",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-weight:400;font-size:inherit;word-break:break-all}html,body{width:100%;height:100%;color:#111}body{background-color:#fff}body{cursor:url("../images/mouse.png") 20 20,auto;-webkit-user-select:text;-moz-user-select:text;-ms-user-select:text;user-select:text}h1,h2,h3,h4,p{margin:0}ul,ol,li,dl,dt,dd{list-style:none;margin:0;padding:0}button{padding:0;border:0;background-color:rgba(0,0,0,0);cursor:pointer}a{color:inherit;text-decoration:none}a:hover,button:hover,label:hover{cursor:url("../images/mouse_link.png") 20 20,auto}#wrapper{min-height:100vh;background-color:#fff}#container{position:relative;height:100%;min-height:100vh;padding:0;box-sizing:border-box;background-color:#ebe6da}#header{position:fixed;display:flex;width:100%;padding:0 20px;box-sizing:border-box;align-items:center;justify-content:space-between;z-index:7}#header.difference{mix-blend-mode:difference}#header.white .header-logo{filter:brightness(0) invert(1)}#header.white .btn-music{color:#eee;border-color:#ddd;filter:brightness(0) invert(1);opacity:.8;background-attachment:initial}#header.white #btn-menu{z-index:7}#header.white #btn-menu .line{background-color:#fff}#header.white #btn-menu .line:before,#header.white #btn-menu .line:after{background-color:#fff}#header.white #btn-menu.open .line{background-color:rgba(0,0,0,0)}#header .header-logo{position:relative;z-index:7}#header .header-logo img{display:block;width:100%;height:50px}#header .btn-music{position:relative;display:block;width:95px;min-width:95px;height:30px;padding:5px 10px;padding-right:30px;border:2px solid #111;border-radius:999px;background-repeat:no-repeat;background-size:20px 20px;background-position:center right 5px;font-size:14px;font-weight:700;z-index:7}#header .btn-music.play{background-image:url("../images/icon_pause.png")}#header .btn-music.pause{background-image:url("../images/icon_play.png")}#btn-menu{position:relative;min-width:30px;min-height:30px;z-index:5}#btn-menu .line{display:block;position:relative;width:24px;height:3px;margin:0 auto;background-color:#111}#btn-menu .line:after{content:"";position:absolute;top:-8px;left:0;width:24px;height:3px;background-color:#111;transition:all .3s}#btn-menu .line:before{content:"";position:absolute;top:8px;left:0;width:24px;height:3px;background-color:#111;transition:all .3s}#btn-menu.open .line{background-color:rgba(0,0,0,0)}#btn-menu.open .line:after{top:50%;transform:rotate(45deg)}#btn-menu.open .line:before{top:50%;transform:rotate(-45deg)}.menu-wrap{position:fixed;top:0;left:-100%;width:400px;height:100vh;background-color:rgba(255,255,255,.8);backdrop-filter:blur(5px);overflow:hidden;z-index:5}.menu-wrap ul{display:flex;flex-direction:column;gap:24px;position:absolute;top:50%;left:50%;width:fit-content;transform:translate(-50%, -50%)}.menu-wrap ul li{display:block;width:100%;height:100%}.menu-wrap ul li a{display:block;width:100%;font-size:30px;font-family:"Galmuri11",Apple SD Gothic Neo,돋움,dotum,sans-serif;color:#111}.menu-wrap ul li a:hover{color:#0095ac}.menu-wrap ul li a.active{color:#0095ac}.menu-wrap.on{left:0;transition:left .5s}.menu-wrap.on a{animation:fadeInUp 3s}@-webkit-keyframes fadeInUp{0%{opacity:0;transform:translate3d(0, 100%, 0)}to{opacity:1;transform:translateZ(0)}}@keyframes fadeInUp{0%{opacity:0;transform:translate3d(0, 100%, 0)}to{opacity:1;transform:translateZ(0)}}@media(max-width: 768px){.menu-wrap{width:100%;height:30%;top:-100%;left:0;padding-top:50px}.menu-wrap ul{margin-top:25px;gap:16px}.menu-wrap ul li a{font-size:20px}.menu-wrap.on{top:0;transition:top .5s}}.footer{position:relative;background-color:#111}.footer-inner{padding:15px 20px;box-sizing:border-box}.footer p{font-size:13px;color:#fff;text-align:center}@media(max-width: 500px){#header .btn-music{width:30px;min-width:30px;height:30px;padding:0;padding-right:0;border:0;background-size:24px 24px;background-position:center center;text-indent:-99999px}}